@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@200;300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Spartan", "sans-serif";
}

:root {
  --PrimaryColor:	#3d5cb8;
  --SecondColor: #f0f4f4;
  --hoverColor: #40B5AD;
  --whiteColor: rgb(250, 249, 246);
  --blackClor: #1f1f1f;
  --textColorOne: #708090;
  --textColor: #36454F;
  --btnColor:#40B5AD ;
  --btnColor2:#90e0db ;
  --lighGrey: #C0C0C0;
  --boxShdow: box-shadow : rgba(0,0,0,0.1) 0px 10px 15px -3px,
  rgba(0,0,0,0.5) 0px 4px 6px -2px;
  --transition: 4s ease-in-out;
}

h4 {
  padding: 1rem 0;
  font-weight: 700;
  color: var(--textColor);
  font-family: "roboto";
}

h2 {
  font-size: 25px;
  padding: 1rem 0;
  color: var(--textColor);
  font-family: "roboto";
}

p {
  font-size: 13px;
  color: var(--textColorOne);
  line-height: 20px;
  font-family: "roboto";
}

body {
  background-color: white;
}

li {
  list-style: none;
}

.navLink {
  text-decoration: none;
  cursor: pointer;
  text-transform: capitalize;
  color: var(--textColorOne);
  margin: 1rem 1rem 0rem 1rem;
  font-family: "roboto";
  font-weight: 500;
  transition: all 0.4s; /* Reduced the duration to 0.3 seconds */
}
.navLink:hover {
  color: var(--hoverColor);
  transform: translateY(10px);
}

.flex {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  align-items: center;
}

.icon {
  font-size: 1.2rem;
  cursor: pointer;
}

.none {
  display: none;
}

.btn {
  background: var(--btnColor);
  border: none;
  outline: none;
  border-radius: 10px;
  width: 100px;
  height: 40px;
  cursor: pointer;
  font-weight: 800;
  color: var(--whiteColor);
}
.btn:hover {
  background-color: var(--PrimaryColor);
}

img {
  width: 100%;
}

input {
  font-size: 100%;
  color: var(--textColor);
}

.section {
  padding: 4rem 0 2rem;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.navbar {
  justify-content: space-between;
  width: 100%;
  position: fixed;
  top: 0px;
  z-index: 1500;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px -3px;
}
.navbar .navbarMenu {
  margin-right: 1.5rem;
}
.navbar .logoDiv {
  margin-top: -35px;
  margin-left: 20px;
  margin-bottom: -40px;
}
.navbar .logoDiv .logo {
  width: 150px;
}
.navbar .toggleIcon {
  display: none;
}
.navbar .cancel {
  display: none;
}
@media screen and (max-width: 768px) {
  .navbar .navbarMenu {
    position: absolute;
    background-color: var(--SecondColor);
    padding: 1.5rem;
    border-radius: 10px;
    top: 5rem;
    right: -50%;
    width: 60%;
    transform: translateX(60%);
    z-index: 2000;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.1) 0px 4px 6px -2px;
    border: 2px solid var(--whiteColor);
    transition: 0.5s ease-in-out;
  }
  .navbar .navbarMenu .cancel {
    position: absolute;
    left: 90%;
    top: 5px;
    color: var(--textColorOne);
    display: block;
  }
  .navbar .navbarMenu .menu {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  .navbar .navbarMenu .menu .navLink {
    padding: -5rem 0;
    font-size: larger;
    color: var(--textColorOne);
    cursor: pointer;
  }
  .navbar .navbarMenu .menu .navLink:hover {
    color: var(--hoverColor);
  }
  .navbar .toggleIcon {
    display: block;
    margin-right: 20px;
  }
  .navbar .toggleIcon .icon {
    font-size: 30px;
    color: var(--textColorOne);
  }
  .navbar .showMenu {
    right: 50%;
    transition: 0.5s ease-in-out;
  }
}

.containerHome {
  margin-top: 74px;
}
.containerHome .homeImage {
  position: relative;
  width: 100%;
  height: 600px;
  background-image: url("./assests/Images/homeImage3.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 1000;
}
.containerHome .homeImage::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(115, 147, 179, 0.66);
}
.containerHome .content {
  position: absolute;
  top: 25%;
  left: 40%;
  transform: translate(-30%, -30%);
  z-index: 1000;
  width: 600px;
  transition: 0.1s ease-in-out;
}
.containerHome .content h2 {
  font-size: 25px;
  padding: 0.5rem;
  color: white;
  font-weight: bolder;
  font-size: 50px;
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  letter-spacing: 1px;
}
.containerHome .content p {
  font-weight: 700;
  color: white;
  text-align: center;
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
@media screen and (max-width: 760px) {
  .containerHome .content {
    width: 300px;
    top: 25%;
    left: 40%;
  }
  .containerHome .content h2 {
    font-size: 30px;
  }
  .containerHome .content p {
    font-size: 10px;
  }
}

