@keyframes show {
    0%, 49.99% {
       opacity: 0;
       z-index: 1;
    }
 
    50%, 100% {
       opacity: 1;
       z-index: 5;
    }
 }
 

.containerSignIn {
    background-color: #f6f5f7;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'roboto', sans-serif;
    height: 100vh;
    margin: 60px 0 50px;
 }
 
 .containerSignIn h1 {
    font-weight: bold;
    margin: 0;
    color: #36454F;
 }

 
 
 .containerSignIn p {
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0.5px;
    margin: 20px 0 30px;
    color: white;
 }
 
 .containerSignIn span {
    font-size: 12px;
    color: #36454F;
 }
 
 .containerSignIn a {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
 }
 
 .containerSignIn button {
    border-radius: 20px;
    border: 1px solid var(--btnColor);
    background-color: var(--btnColor);
    color: #ffffff;
    font-size: 12px;
    font-weight: bold;
    padding: 12px 45px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
 }
 
 .containerSignIn button:active {
    transform: scale(0.95);
 }
 
 .containerSignIn button:focus {
    outline: none;
 }
 
 .containerSignIn .ghost {
    background-color: transparent;
    border-color: #ffffff;
 }
 
 .containerSignIn form {
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    text-align: center;
 }
 
 .containerSignIn form input {
    background-color: #eee;
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
 }
 
 .containerSignIn .container {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 14px 20px rgba(0, 0, 0, 0.25),
       0 10px 10px rgba(0, 0, 0, 0.22);
    position: relative;
    overflow: hidden;
    width: 1100px;
    max-width: 100%;
    min-height: 480px;
 }
 
.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
 }
 
 .sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
 }

 .container.right-panel-active .sign-in-container {
    transform: translateX(100%);
 }
 
 .sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
 }

 .container.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.5s;
 }
 
 .overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
 }

 .container.right-panel-active .overlay-container {
    transform: translateX(-100%);
 }
 
  .overlay {
    background: var(--btnColor);
    background: -webkit-linear-gradient(to right, #40B5AD, #90e0db);
    background: linear-gradient(to right #40B5AD #90e0db);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    color: #ffffff;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
 }
 
 
 .container.right-panel-active .overlay {
    transform: translateX(50%);
 }

 .overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
 }
 
  .overlay-left {
    transform: translateX(0%);
 }
 
 .container.right-panel-active .overlay-left {
    transform: translateX(0%);
 }
 
 .overlay-right {
    right: 0;
    transform: translateX(0%);
 }

 .container.right-panel-active .overlay-right {
    transform: translateX(20%);
 }
 
 
 .social-container {
    margin: 20px 0;
 }
 
 .social-container a {
    border: 1px solid #dddddd;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    height: 40px;
    width: 40px;
 }
 
 
 
 
 
 
 
 

 
